@font-face {
  font-family: "Avenir";
  src: url("./fonts/Avenir-Book.ttf");
}

* {
  box-sizing: border-box;
}

body {
  font-family: Avenir, sans-serif;

}

.box {
  position: relative;
  margin: 20px 0;
}

.icon {
  position: absolute;
}

.big-img {
  width: 19px;
  height: 19px;
  margin: 25px 0 0 26px;
}

.small-img {
  width: 11px;
  height: 11px;
  margin: 15px 0 0 13px;
}

.bar {
  width: 100%;
  background-color: #fff;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  color: #3d4e61;
  font-family: inherit;
  font-weight: 700;
}

.bar:hover {
  box-shadow: 0 3px 8px rgba(61,78,97,2);
}

.bar:focus {
  outline: none;
  background: linear-gradient(180deg,#fff,#f6f6f7);
  border-radius: 3px;
}

.bar::placeholder {
  font-weight: 300;
}

.big-input {
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
  line-height: 22px;
}

.small-input {
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
  line-height: 19px;
}
